<template>
    <footer id="Footer"  v-show="searchVis">
        <div class="footer-inner">
            <span class="tech" v-for="item in tech">
                {{item.name}}
                <span class="edition">{{'@'+item.edition}}</span>
            </span>
        </div>
    </footer>
</template>
<script>
    export default {
        data () {
            return {
                searchVis: true,
                tech: [
                    { name: 'vue', edition: '2.5.13' },
                    { name: 'elementUi', edition: '2.0.11' }
                ]
            };
        }
    };
</script>
<style lang="scss">
    @import './../assets/css/conifg/basic.config.scss';

    // 底部
    #Footer{

        & {
            width: 100%;
            z-index: 3;
            overflow: hidden;
            min-height: 60px;
            background: $primary-color + #240024;
            color: $primary-color + #8b408b;
            text-align: center;
            display: table;
            @include transition;
        }

        &:hover {
            color: #F9FAFC;
        }

        .footer-inner {
            display: table-cell;
            vertical-align: middle;
        }

        .tech {
            margin:4px;
        }
    }

    @media screen and ( max-width: 768px ) {
        #Footer .tech .edition {
            display: none;
            background: $primary-color;
        }
        #Footer {
            height: 36px;
            min-height: 36px;
            font-size: 12px;
            color: #f9fafc;
        }
    }
</style>


